<template>
  <div class="funds-deposit">
    <div style="position: fixed; top: 50%; left: 50%; z-index: 999;">
      <atom-spinner
        v-if="loading"
        :animation-duration="speed"
        :size="config.size"
        :color="spinnersColor"
      />
    </div>
    <div class="mb-3" v-if="processingError">
      <va-notification color="danger" closeable>       
        An error occurred! Please Try again later.
      </va-notification>
    </div>
    <div class="row">
      <div class="flex xs12 md6 lg6">
        <va-card :title="$t('activities.password_form')">
          <form @submit.prevent="onsubmit">
            <va-input
              label="Edit Router Name Optional"
              type="text"
              v-model="formdata.name"
            />
            <va-input
              class="mb-4"
              label="Edit Password"
              type="password"
              v-model="formdata.password"
              :error="!!passwordErrors.length"
              :error-messages="passwordErrors"
            />

            <va-input
              label="Confirm Password"
              type="password"
              v-model="formdata.confirm"
              :error="!!passwordErrors.length"
              :error-messages="passwordErrors"
            />

            <div class="row mt-5">            
              <va-button type="submit" >
                {{ $t('forms.controls.submit') }}
              </va-button>                        
            </div>
          </form>
        </va-card>
      </div>
    </div>
    <va-modal
      v-model="showConfirmModal"
      position="top"
      size = "medium"
      :title=" $t('confirm_trans')"
      cancelClass="none"      
      :okText=" $t('modal.confirm') "     
      :cancelText=" $t('modal.cancel')"      
      noOutsideDismiss
      noEscDismiss
      @ok = "FinalPost"
    >
      <table class="va-table va-table--striped va-table--hoverable" width="100%">
        <tbody>
          <!-- <tr>
            <td>Router Name</td>
            <td style="text-align: right;">{{ router_name }}</td>
          </tr> -->
          
          <tr>
            <td>Password</td>
            <td style="text-align: right;"> {{ new_pass }} </td>
          </tr>                            
        </tbody>
      </table>
    </va-modal>
  </div>
</template>
<script>
import axios from 'axios'
import * as spinners from 'epic-spinners'
export default {
  components: {
    ...spinners      
  },
  name: 'funds-deposit',
  data () {
    return {
      userData: JSON.parse(localStorage.getItem('LoggedUser')),     
      token: JSON.parse(localStorage.getItem('Token')), 
      loading: false,
      processingError: false,
      loadSelect:false,
      showConfirmModal : false,
      new_pass:'',
      router_name:'',
      config: {
        size: 80,
        duration: 1500,
      },
      spinnersColor: this.$themes.primary,
      currentDuration: 1500,
      sliderDuration: {
        min: 1000,
        max: 2000,
      },
      formdata:{
        name:'',
        password: '',
        confirm: '',
      },
      passwordErrors: [],
      showBosa: false,
      showFosa: false,
      acc_code : {},
      selected : {},
      postdata : {},
    }
  },
  computed: {
    speed () {
      return this.sliderDuration.min + this.sliderDuration.max - this.currentDuration
    },

    formReady () {
        return !this.passwordErrors.length
    }
  }, 
  methods : {
    getSelected(acc_code){
      console.log(acc_code)
      return this.accounts.find(item => 
        item.paybill_code.toLowerCase() == acc_code.toLowerCase()
      )
    },
    onsubmit () {
      this.processingError = false;
      this.passwordErrors = this.formdata.password == this.formdata.confirm ? [] : ['Password do not match']

      if (this.formReady) {        
        // this.router_name = this.formdata.name         
          
        this.new_pass = this.formdata.password;
        this.showConfirmModal = true;
      }
    }, 

    FinalPost () {
      const selected = this.formdata.password
      const msg = "Password Changed Successfully";
      this.loading = true
      var self = this

      let config = {
        headers: {
          Authorization: this.userData.user_id+' '+this.token
        }
      }


      axios.post(`${this.$apiUrl}ChangeWifiPass`, this.formdata, config)
      .then(function (res) {
        self.loading = false        
        const resp = res.data
        if(resp.response.success == '1'){              
          self.showToast(`Password Changed Successfully`, {
            position: 'bottom-right',
            icon: "fa-check",
            duration: 2500,
          })
          //setTimeout( () => self.$router.go(-1), 2500);
        } else {
          self.processingError = true
        }                  
      })
      .catch(function (error) {
          console.log(error)
          self.loading = false 
      })
    },

    

    setSelected(value){
      this.formdata.acc_code = {};
      const val = value.id
      if (val) {
        if (val == 'fosa'){
          this.accounts = this.fosas
          this.loadSelect = false;
          this.showFosa = true
          this.showBosa = false
        } else {
          this.showBosa = true
          this.showFosa = false
          this.loadBosaAccs();
        } 
      }
    }
  },
  created () { 
    this.formdata.phone_no  = this.userData.mobile_number
    this.formdata.user  = this.userData.user_id    
  } 
} 
</script>
   
